export default {
	data() {
		return {
			epoint: '2,500',
			coupons:0
		}
	},
	methods: {
		minus() {
			if(this.coupons==0){
				uni.showToast({
					title: '不能在减了!',
					icon:'none'
				});
			}else{
				this.coupons--
			}
		},
		add:function(){
			this.coupons++
		}
	},
}
